.axisGroupX path {
  stroke: none;
}
.tick {
  transform: translateX(-10px);
}
svg > .axisGroupY > g > text {
  transform: translateX(-0.5rem);
}
svg > .axisGroupX > g > text {
  transform: translate(-0.5rem, 1.6rem) rotate(-90deg);
}


svg > .axisGroupY > g > line {
  fill:var(--white);
  stroke: var(--white);/*for dark graph theme*/
  opacity: 0.2;
}
svg > .axisGroupX > g > line {
  fill:var(--white);
  stroke: var(--white);/*for dark graph theme*/
  opacity: 0.2;
}

.axisGroupY :nth-child(1) {
  stroke-linejoin: round;
}

/* For dark theme graph */
.axisGroupX  {
  color:var(--white);
}

.axisGroupY  {
  color:var(--white);
}

/* graph border */
.axisGroupX, .axisGroupY{
  color: var(--white);
}
.axisGroupX path, .axisGroupY path{
  stroke: var(--white);
  opacity: 0.2;
}